// Different order for grid in portfolio, adding class
const getImageOrientation = (index, odd) => {
  if (odd) {
    switch (index) {
      case 1:
        return 'horizontal';
      case 3:
        return 'big';
      case 4:
        return 'horizontal';
      case 5:
        return 'vertical';
      case 6:
        return 'vertical';
      default:
        return '';
    }
  } else {
    switch (index) {
      case 2:
        return 'horizontal';
      case 3:
        return 'horizontal';
      case 4:
        return 'big';
      case 5:
        return 'vertical';
      case 6:
        return 'vertical';
      default:
        return '';
    }
  }
  return;
};

